import React,{Component} from 'react'
//import {Text,TextInput,View,TouchableOpacity} from 'react-native'
//import RadioForm, {RadioButton, RadioButtonInput, RadioButtonLabel} from 'react-native-simple-radio-button';
import PropTypes from "prop-types";
//import { injectNOS, nosProps } from "@nosplatform/api-functions/lib/react";
import { Link } from "react-router-dom";

const nos = window.NOS.V1;
const scriptHash = '0dfbdd64e96689d5f4cfe5d289bf96bd5b270a1b';

const getParameterByName = (name, url) => {
  if (!url) url = window.location.href;
  name = name.replace(/[\[\]]/g, "\\$&");
  var regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)"),
    results = regex.exec(url);
  if (!results) return null;
  if (!results[2]) return "";
  return decodeURIComponent(results[2].replace(/\+/g, " "));
};

class GetResult1 extends Component{
    constructor(props){
        super(props)
    }
    render(){
        const question = getParameterByName("question", window.href);
        const yes = getParameterByName('y', window.href)
        const no = getParameterByName('n', window.href)
        return(
            <div>

            <div style={{ flex: 1 }}>
        <h4><strong>Poll Question:</strong></h4>
          <span style={{fontSize: '16px'}}>{question}</span>
          <h4><strong>Vote:</strong></h4>
         
            

                <ul>
            <li>
              <label>
            <span style={{fontSize: '16px'}}><strong>Yes: </strong>{yes} </span>
            </label>
            </li>
           
             
            <li>
              <label>
            <span style={{fontSize: '16px'}}><strong>No: </strong>{no} </span>
            </label>
            </li>
            </ul>
            <button style={{
            backgroundColor: "gray",
            width: 100,
            height: 50,
            padding: 15,
            marginTop: 20,
            marginLeft: 160
          }} onClick={() => this.props.history.push(`/`)}>Ok
          </button>
          </div>
         </div>
         
        );
    }
}
export default GetResult1;
