import React,{Component} from 'react'
//import {Text,TextInput,View,TouchableOpacity} from 'react-native'
//import RadioForm, {RadioButton, RadioButtonInput, RadioButtonLabel} from 'react-native-simple-radio-button';
import PropTypes from "prop-types";
//import { injectNOS, nosProps } from "@nosplatform/api-functions/lib/react";
import { Link } from "react-router-dom";
import { withRouter } from "react-router-dom";

const nos = window.NOS.V1;
const scriptHash = '0dfbdd64e96689d5f4cfe5d289bf96bd5b270a1b';


const getParameterByName = (name, url) => {
  if (!url) url = window.location.href;
  name = name.replace(/[\[\]]/g, "\\$&");
  var regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)"),
    results = regex.exec(url);
  if (!results) return null;
  if (!results[2]) return "";
  return decodeURIComponent(results[2].replace(/\+/g, " "));
};



class AccessPoll1 extends Component{
    constructor(props){
        super(props)
        this.state = {
            size: ''
          };
    }

    setPoll(){
      const vote_num = this.state.vote
      console.log("voted: "+ vote_num)
      // const id = "1";
      const id = getParameterByName("id", window.href);
      nos.getAddress()
          .then(adress => {
            //alert(adress);

            // get poll id
            nos.invoke({
              scriptHash,
              operation: "vote",
              args: [
                adress,
                //'AeqRuKKbFu7KoeQHcNTnSUdjcak6YkAfhJ',
                //this.state.size,
                id,
                //"1",
                vote_num
              ]
          })
              .then(response => {
                alert(`Your vote is cast to ${this.state.vote_text}`)
                // alert(response);
                // alert(adress); //AK2nJJpJr6o664CWJKi1QRXjqeic2zRp8y
                // var asdKEY = `${adress}+${response}`;
                // alert(asdKEY);
                // const asdKEY2 = 'AK2nJJpJr6o664CWJKi1QRXjqeic2zRp8y+8012b4e67209dff815868fecbe49ef8fd72661ac2993fc0d7d2c5869cbe37877';

                // nos.getStorage({ scriptHash: scriptHash, key: asdKEY2})
                //     .then(r=>alert(`getStorage: ${r}`))
                //     .catch(e=>alert(`Error getStorage: ${e}`));
              })
              .catch((err) => alert(`Error: ${err.message}`))
          });
    }


    render(){
      const question = getParameterByName("q", window.href);
      return(
        <div>
        <div style={{ flex: 1 }}>
        <h4><strong>Poll Question:</strong></h4>
          <span style={{fontSize: '16px'}}>{question}</span>
          <h4><strong>Vote:</strong></h4>
          <ul>
            <li>
              <label>
                <input
                  type="radio"
                  value="1"
                  checked={this.state.vote === "1"}
                  onChange={() => this.setState({ vote: "1", vote_text: "Yes" })}
                />
                yes
              </label>
            </li>

            <li>
              <label>
                <input
                  type="radio"
                  value="2"
                  checked={this.state.vote === "2"}
                  onChange={() => this.setState({ vote: "2", vote_text: "No" })}
                />
                no
              </label>
            </li>
          </ul>
          <button style={{
            backgroundColor: "gray",
            width: 100,
            height: 50,
            padding: 15,
            margin: 50
          }}
                  onClick={() => {
                    this.setPoll();
                  }}>
            Submit Vote
          </button>

           <button style={{
            backgroundColor: "gray",
            width: 100,
            height: 50,
            padding: 15,
            margin: 50
          }} onClick={() => this.props.history.push(`/`)}>Cancel
          </button>

        </div>
        </div>
        );
    }

}
export default withRouter(AccessPoll1);
