import "babel-polyfill";
import React from "react";
import ReactDOM from "react-dom";

import { create as createJss } from "jss";
import camelCase from "jss-camel-case";
import globalStyles from "jss-global";
import vendorPrefixer from "jss-vendor-prefixer";
import { JssProvider } from "react-jss";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";

import App from "./views/App";
import CreatePoll from "./views/CreatePoll";
import AccessPoll from "./views/AccessPoll";
import AccessPoll1 from "./views/AccessPoll1";
import AccessPoll2 from "./views/AccessPoll2";
import GetResult from "./views/GetResult";
import GetResult1 from "./views/GetResult1";


const jss = createJss();
jss.use(vendorPrefixer(), camelCase(), globalStyles());

ReactDOM.render(

  <JssProvider jss={jss}>

    <Router>
      <div>

        {/*<Link to="/" style={{backgroundColor:'green',width:100,height:50,padding:15,margin:30}}>App</Link>*/}

 <Link to="/createpoll">
     <button type="button" style={{
            backgroundColor: "red",
            width: 100,
            height: 50,
            padding: 15,
            margin: 20
          }}>
          Create Poll
     </button>
 </Link>


 <Link to="/accesspoll">
     <button type="button" style={{
            backgroundColor: "green",
            width: 100,
            height: 50,
            padding: 15,
            margin: 20
          }}>
          Access Poll
     </button>
 </Link>


 <Link to="/getresult">
     <button type="button" style={{
            backgroundColor: "yellow",
            width: 100,
            height: 50,
            padding: 15,
            margin: 20
          }}>
          Get Results
     </button>
 </Link>



        <Route exact path="/" component={App}/>
        <Route path="/createpoll" component={CreatePoll}/>

        <Route path="/accesspoll" component={AccessPoll}/>
        <Route path="/accesspoll1" component={AccessPoll1}/>
        <Route path="/accesspoll2" component={AccessPoll2}/>

        <Route path="/getresult" component={GetResult}/>
        <Route path="/getresult1" component={GetResult1}/>
      </div>
    </Router>
  </JssProvider>
  ,
  document.getElementById("root")
);




