import React, { Component } from "react";
import PropTypes from "prop-types";
import { injectNOS, nosProps } from "@nosplatform/api-functions/lib/react";
import { Link } from "react-router-dom";
import { withRouter } from "react-router-dom";
import utils from "../../utils";
import { addListener } from "cluster";


const nos = window.NOS.V1;
const scriptHash = "0dfbdd64e96689d5f4cfe5d289bf96bd5b270a1b";
function makeid() {
  var text = "";
  var possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";

  for (var i = 0; i < 8; i++)
    text += possible.charAt(Math.floor(Math.random() * possible.length));
  return text;
}

class CreatePoll extends Component {
  constructor(props) {
    super(props);
    this.state = {
      size: ""
    };
  }


  yourFunction() {
    const id = makeid()
  
    try {

      nos.getAddress()
        .then(adress => {
           nos.invoke({
            scriptHash,
            operation: "create",
            args: [
              adress,
               id,
              this.state.q
            ]
        })
            .then(response => {
              console.log(response);
              this.setState({id: id})
            })
            .catch((err) => alert(`Error: ${err.message}`))

        });


    } catch (e) {
      alert(`value ${e}`);
      return;
    }
  }


  render() {

    return (

      <div>
        <h4><strong>Insert poll Question:</strong></h4>
        <input placeholder="write here your poll..."
                 onChange={(e) => this.setState({ q: e.target.value })}
                 style={{
                   height: 20,
                   backgroundColor: "#ffffff",
                   paddingLeft: 15,
                   paddingRight: 15
                 }}></input>

        <div style={{ display: "flex", flexDirection: "row", justifyContent: "left" }}>
          <button style={{
            backgroundColor: "gray",
            width: 100,
            height: 50,
            padding: 15,
            margin: 50
          }}
                  onClick={() => {
                    this.yourFunction();
                  }}>
            Submit
          </button>

          <button style={{
            backgroundColor: "gray",
            width: 100,
            height: 50,
            padding: 15,
            margin: 50
          }} onClick={() => this.props.history.push(`/`)}>Cancel</button>
        </div>
        <h4><strong>The poll created ID: {this.state.id ? this.state.id: "NA"}</strong></h4>
      </div>
    );
  }
}

export default withRouter(CreatePoll);